.site-list {
  @include ziro-intends;
  position: relative;
  width: 320px;
  margin: 0 auto;
  text-align: center;
  list-style-type: none;
}

@media (min-width: $tablet-width) {
  .site-list {
    display: grid;
    grid-template-columns: 64px 154px 153px;
    column-gap: 33px;
    width: 440px;
  }
}

@media (min-width: $desktop-width) {
  .site-list {
    grid-template-columns: 71px 171px 170px;
    width: 478px;
  }
}

@media (min-width: $desktop-width) {
  .site-list--color {
    background-color: $white;
  }
}

.site-list__item {
  position: relative;
  width: 280px;
  padding: 23px 20px 24px 20px;
  margin: 0 auto;
  font-family: "Oswald", sans-serif;
  color: $black;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

@media (max-width: $mobile-width-only) {
  .site-list__item::after {
    position: absolute;
    content: "";
    top: 0;
    left: -500px;
    height: 1px;
    width: 100000%;
    background-color: $gray;
  }
}

@media (min-width: $tablet-width) {
  .site-list__item {
    @include ziro-intends;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    border: none;
    text-align: left;
  }
}

@media (min-width: $tablet-width) {
  .site-list__item:not(:last-child) {
    border-bottom: none;
  }
}

@media (min-width: $desktop-width) {
  .site-list__item {
    font-size: 20px;
    line-height: 30px;
    z-index: 2;
  }
}

.site-list__link {
  color: $black;
  text-decoration: none;
}

@media (min-width: $tablet-width) {
  .site-list__link {
    color: $black;
    text-decoration: none;
  }
}

@media (min-width: $tablet-width) {
  .site-list__link--current {
    position: relative;

    &::after {
      position: absolute;
      content: "";
      top: calc(100% + 10px);
      left: 0;
      height: 2px;
      background-color: $basic-green;
      width: 100%;
      z-index: 2;
    }
  }
}

@media (min-width: $desktop-width) {
  .site-list__link {
    font-size: 20px;
    line-height: 30px;
    z-index: 2;
  }
}

@media (min-width: $desktop-width) {
  .site-list__link--current1 {
    position: relative;
  }

  .site-list__link--current1::after {
    position: absolute;
    content: "";
    top: calc(100% + 10px);
    left: 0;
    height: 2px;
    background-color: $white;
    width: 100%;
    z-index: 2;
  }
}

@media (min-width: $desktop-width) {
  .site-list__link--color1 {
    color: $white;
  }
}
